<template lang="html">
  <section>
  <v-toolbar tabs app color="#bf4037" dark>
     <!-- <v-toolbar-side-icon
       @click.stop="drawer = !drawer"
     ></v-toolbar-side-icon> -->
     <v-toolbar-title>
      <v-select
        color="#fff"
        @change="selectTasks()"
        v-model="filter"
        style="width:120px;"
        :items="filters"
        placeholder="Фильтр"
      ></v-select>
     </v-toolbar-title>
     <v-spacer></v-spacer>
     <v-menu bottom left>
          <template v-slot:activator="{ on }">
            <v-btn
              dark
              icon
              v-on="on"
            >
              <v-icon>more_vert</v-icon>
            </v-btn>
          </template>

          <v-list>
            <v-list-tile
              v-for="(item, i) in items"
              :key="i"
              @click="linkTo(item)"dark
            >
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
   </v-toolbar>


        <!-- ТАБЫ -->
        <v-tabs
          slider-color="#bf4037"
          max="110%" 
          color="#424242"
          grow
        >
          <v-tab
            style="color:#fff;"
            v-for="tab in tabs"
            :key="tab.id"
            ripple
          >
            {{ tab.status }}

          </v-tab>
            <v-tab-item
              v-for="tas in tabs"
            >
              <v-layout row wrap style="margin-top:5px;">
             <v-flex xs12 
             v-for="task in tas.tasks">
                <v-card @click="task.state = !task.state" style="padding:20px;position:relative;margin-bottom:5px;" class="mx-auto">
                  <div v-if="task.status === 'В работе'" style="background-color:#2046e6;position:absolute;left:0;top:0;height:100%;width:10px;"></div>
                  <div v-if="task.status === 'Готово'" style="background-color:green;position:absolute;left:0;top:0;height:100%;width:10px;"></div>
                  <div v-if="task.status === 'Ожидает'" style="background-color:orange;position:absolute;left:0;top:0;height:100%;width:10px;"></div>
                  <v-card-text style="padding:0;display:flex;justify-content:space-between;margin-borrom 20px;">
                    <ul class="task_list" style="list-style-type:none;padding:0;">
                      <li style="font-size:16px;font-weight:bold;">{{task.customer}}</li>
                      <li style="color:grey;">{{task.address}}</li>
                    </ul>
                    <ul class="task_list" style="list-style-type:none;padding:0;">
                      <li v-if="task.status === 'В работе'" style="color:#2046e6;font-size:16px;font-weight:bold;">{{task.status}}</li>
                      <li v-if="task.status === 'Готово'" style="color:green;font-size:16px;font-weight:bold;">{{task.status}}</li>
                      <li v-if="task.status === 'Ожидает'" style="color:orange;font-size:16px;font-weight:bold;">{{task.status}}</li>
                      <li style="color:grey;">{{task.timer}}</li>
                    </ul>
                  </v-card-text>
                  <transition name="fade">
                  <v-card-text v-show="task.state" class="more_info">
                    <ul style="list-style-type:none;padding:0;">
                      <li style="color:grey;margin-bottom:10px;"><span style="color:#424242;font-weight:bold;">Менеджер: </span> <span>{{task.manager}}</span></li>
                      <li style="color:grey;margin-bottom:10px;"><span style="color:#424242;font-weight:bold;">Задача: </span> <span>{{task.task}}</span></li>
                      <li style="color:grey;margin-bottom:10px;"><span style="color:#424242;font-weight:bold;">Что взять: </span> <span>{{task.invent}}</span></li>
                      <li style="color:grey;margin-bottom:10px;"><span style="color:#424242;font-weight:bold;">Номер заказчика: </span> <span>{{task.phone}}</span></li>
                    </ul>
                  </v-card-text>
                  </transition>
                </v-card>
             </v-flex>
           </v-layout>
          </v-tab-item>
        </v-tabs>




    <!-- НАВИГАЦИОННАЯ ПАНЕЛЬ СЛЕВА -->

   <!-- <v-navigation-drawer
    v-model="drawer"
    :mini-variant="mini"
    absolute
    color="black"
    text-color="#bf4037"
    dark
    temporary
  >
    <v-list class="pa-1">
      <v-list-tile v-if="mini" @click.stop="mini = !mini">
        <v-list-tile-action>
          <v-icon>chevron_right</v-icon>
        </v-list-tile-action>
      </v-list-tile>

      <v-list-tile avatar tag="div">
        <v-list-tile-avatar>
          <v-icon style="font-size:40px;">person_pin</v-icon>
        </v-list-tile-avatar>

        <v-list-tile-content>
          <v-list-tile-title>John Leider</v-list-tile-title>
        </v-list-tile-content>

        <v-list-tile-action>
          <v-btn icon @click.stop="mini = !mini">
            <v-icon>chevron_left</v-icon>
          </v-btn>
        </v-list-tile-action>
      </v-list-tile>
    </v-list>

    <v-list class="pt-0" dense>
      <v-divider dark></v-divider>

      <v-list-tile
        class="menu_devider"
        v-for="item in items"
        :key="item.title"
        @click=""
        :to="item.path"
        caret-color="#bf4037"
      >
        <v-list-tile-action>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-tile-action>

        <v-list-tile-content>
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer> -->
  </section>
</template>

<script>
import router from "../router"
export default {
  data() {
    return {
      active: null,
      stateses: false,
      filter: "Все задачи",
      drawer: null,
      filters: ["Все задачи", "Готово", "Ожидает", "В работе"],
      items: [
        {
          title: "Выход",
          icon: "exit_to_app",
          path: "/"
        }
      ],
      tasksFilter1: [
        {
          customer: "Бауцентр",
          address: "ул. Золотая Рыбка, 23, Новорос...",
          timer: "Время: 18:00",
          status: "В работе",
          manager: "Киркорова А. М.",
          task: "Убить Билла",
          invent: "Самурайская катана",
          phone: "+7 (988) 31-47-183",
          state: false
        },
        {
          customer: "ТЦ Красная площадь",
          address: "ул. Анапское шоссе, 2, Новорос...",
          timer: "Время: 12:40",
          status: "Ожидает",
          manager: "Киркорова А. М.",
          task: "Баннер цветной над входом",
          invent: "Жвачка,изолента,мололток",
          phone: "+7 (988) 31-47-183",
          state: false
        },
        {
          customer: "Nippon Auto",
          address: "Промышленная 3 а, Новорос...",
          timer: "Время: 10:15",
          status: "Готово",
          manager: "Киркорова А. М.",
          task: "Согласовать дизайн флаеров",
          invent: "Дизайн в формате PSD на флешке",
          phone: "+7 (988) 31-47-183",
          state: false
        }
      ],
      tasksFilter2: [
        {
          customer: "Бон Пассаж",
          address: "Анапское шоссе, Новорос...",
          timer: "Время: 18:00",
          status: "В работе",
          manager: "Киркорова А. М.",
          task: "Убить Билла",
          invent: "Самурайская катана",
          phone: "+7 (988) 31-47-183",
          state: false
        },
        {
          customer: "ТЦ Красная площадь",
          address: "ул. Анапское шоссе, 2, Новорос...",
          timer: "Время: 12:40",
          status: "Ожидает",
          manager: "Киркорова А. М.",
          task: "Баннер цветной над входом",
          invent: "Жвачка,изолента,мололток",
          phone: "+7 (988) 31-47-183",
          state: false
        }
      ],
      tasksFilter3: [
        {
          customer: "Nippon Auto",
          address: "Промышленная 3 а, Новорос...",
          timer: "Время: 10:15",
          status: "Готово",
          manager: "Киркорова А. М.",
          task: "Согласовать дизайн флаеров",
          invent: "Дизайн в формате PSD на флешке",
          phone: "+7 (988) 31-47-183",
          state: false
        }
      ],
      tabs: [  
        {
          id: 0,
          status: "Сегодня",
          tasks: []
        },
        {
          id: 1,
          status: "Завтра",
          tasks: []
        },
        {
          id: 2,
          status: "Остальное",
          tasks: []
        }
     ],
      mini: false,
      right: null
    };
  },
  methods: {
    selectTasks() {
      var _this = this
      switch (this.filter) {
        case "Все задачи":
          _this.tabs[0].tasks = []
          _this.tabs[1].tasks = []
          _this.tabs[2].tasks = []
          this.tasksFilter1.forEach(function(element) {
            _this.tabs[0].tasks.push(element)
          })
          this.tasksFilter2.forEach(function(element) {
            _this.tabs[1].tasks.push(element)
          })
          this.tasksFilter3.forEach(function(element) {
            _this.tabs[2].tasks.push(element)
          })
          break;
        case "Готово":
          this.tasksFilter1.forEach(function(element) {
            _this.tabs[0].tasks = []
            if (element.status === "Готово") {
              _this.tabs[0].tasks.push(element)
            }
          })
          this.tasksFilter2.forEach(function(element) {
            _this.tabs[1].tasks = []
            if (element.status === "Готово") {
              _this.tabs[1].tasks.push(element)
            }
          })
          this.tasksFilter3.forEach(function(element) {
            _this.tabs[2].tasks = []
            if (element.status === "Готово") {
              _this.tabs[2].tasks.push(element)
            }
          })
          break;
        case "Ожидает":
          this.tasksFilter1.forEach(function(element) {
            if (element.status === "Ожидает") {
              _this.tabs[0].tasks = []
              _this.tabs[0].tasks.push(element)
            }
          })
          this.tasksFilter2.forEach(function(element) {
            if (element.status === "Ожидает") {
              _this.tabs[1].tasks = []
              _this.tabs[1].tasks.push(element)
            }
          })
          this.tasksFilter3.forEach(function(element) {
            if (element.status === "Ожидает") {
              _this.tabs[2].tasks = []
              _this.tabs[2].tasks.push(element)
            }
          })
          break;
        case "В работе":
          this.tasksFilter1.forEach(function(element) {
            if (element.status === "В работе") {
              _this.tabs[0].tasks = []
              _this.tabs[0].tasks.push(element)
            }
          })
          this.tasksFilter2.forEach(function(element) {
            if (element.status === "В работе") {
              _this.tabs[1].tasks = []
              _this.tabs[1].tasks.push(element)
            }
          })
          this.tasksFilter3.forEach(function(element) {
            if (element.status === "В работе") {
              _this.tabs[2].tasks = []
              _this.tabs[2].tasks.push(element)
            }
          })
          break;
      }
    },
    linkTo(item) {
      router.push({ path: item.path })
    }
  },
  created() {
    this.selectTasks()
  }
};
</script>

<style lang="css">
.v-list {
  padding: 0 !important;
}
.task_list_invis li{
  margin-bottom: 7px;
}
.task_list_invis{
  display: flex;
  justify-content: space-between;
}
.task_list li {
  margin: 7px 0;
}
.more_info{
  padding: 0;
}
.v-toolbar__extension {
  padding: 0 !important;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 1s;
  max-height: 200px;
  overflow: hidden;
}
.fade-enter,
.fade-leave-to {
  transition: all 1s;
  max-height: 0;
  opacity: 0;
}
</style>
