<template>
  <v-container class="login_page">
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card class="login_block" style="background:#424242;">
          <v-card-title class="login_form">Вход</v-card-title>
          <v-card-text>
            <v-form v-model="valid" ref="form" validation>
                <v-text-field
                  dark
                  color="#bf4037"
                  prepend-icon="account_circle"
                  v-model="login"
                  name="login"
                  label="Логин"
                  type="text"
                  :rules="loginRules"
                ></v-text-field>
              <v-text-field
                dark
                color="#bf4037"
                prepend-icon="lock"
                name="password"
                label="Пароль"
                v-model="password"
                :rules="passwordRules"
                @keyup.enter="onLogin()"
                :append-icon="show1 ? 'visibility' : 'visibility_off'"
                :type="show1 ? 'text' : 'password'"
                @click:append="show1 = !show1"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions class="login_btn">
            <v-btn
              style="width:98%;"
              dark color="#bf4037"
              @click="onLogin()"
              class="login_autor"
            >Вход</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import router from '../router'
  export default {
    data () {
      return {
        show1: false,
        valid: false,
        password: '',
        login: '',
        loginRules: [
          v => !!v || 'Введите логин',
          v => v.length >= 12 || 'Неверно'
        ],
        passwordRules: [
          v => !!v || 'Введите пароль',
          v => v.length >= 4 || 'Пароль должен содержать не меньше 4 символов'
        ]
      }
    },
    methods: {
      onLogin() {
        router.push({ path: '/main' })
      }
    }
  }

</script>

<style scoped>
.login_block{
  box-shadow: none;
}
.login_btn {
  display: flex;
  justify-content: center;
}
.login_form {
  color: #fff;
  display: flex;
  justify-content: center;
  font-size: 24px;
}
.login_page {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
